import goodsHeader from './goodsHeader.vue'
import interfaces from '../../utils/interfaces.js'
import popupSpec from '../../components/popupSpec.vue'

export default{
	components:{
		goodsHeader,
		popupSpec
	},
	data(){
		return {
			isKeep: false, // 收藏
			goodsData:{
				swiperList: [],
				spec: [],
				comment: [{
					face: "",
					content: "",
					username: ""
				}],
				descriptionStr: ""
			},
			goodsInfo:{
				name: "",
				price: "",
				number: 1,
				spec: ""
			},
			currentSwiper: 0,  // 轮播图下标
			spaceInfo: {showSpace: false},  // 默认情况下不显示模态框
		}
	},
	onLoad(option) {
		// console.log(option)
		this.goodsInfo = JSON.parse(option.goodsInfo)
		// console.log(this.goodsInfo)
		this.initData()
	},
	methods:{
		// 选择规格
		setSelectSpec(item){
			// console.log(item)
			 this.goodsInfo.spec = item
		},
		// 隐藏选择规格模态框
		hideSpec(){
			this.goodsInfo.spec = item
		},
		// 数量减
		sub(){
			if(this.goodsInfo.number <= 1){
				return
			}
			this.goodsInfo.number--
		},
		// 数量加
		add(){
			this.goodsInfo.number++
		},
		initData(){
			this.request({
				url: interfaces.getGoods,
				success: ((res) => {
					this.goodsData = res.data
					// console.log(this.goodsData)
				})
			})
		},
		// 改变轮播下标数字
		swiperChange(event){
			// console.log(event.detail.current)
			this.currentSwiper = event.detail.current
		},
		// 跳转到评论详情页
		handleRatings(){
			// 本地存储
			uni.setStorageSync('comments',this.goodsData.comment)
			// 页面跳转
			uni.navigateTo({
				url: './ratings'
			})
		},
		// 判断是否收藏
		keep(){
			this.isKeep = !this.isKeep
			// 添加提示框
			if(this.isKeep){
				uni.showToast({
					icon: "success",
					title: "已收藏"
				})
			}
		},
		// 加入购物车
		joinCart(){
			console.log(this.goodsInfo)
			// 存储到本地存储
			// 1.先去本地存储中取
			uni.getStorage({
				key: "goodsList",
				success:((res) =>{
					// 拿数据
					console.log(res.data)
					let goodsList = res.data
					// 查找商品是否存在
					let isExist = false
					
					// 如果商品存在
					goodsList.forEach(goods => {
						// 判断商品ID和规格是否一致
						if(goods.goods_id == this.goodsInfo.goods_id && goods.spec == this.goodsInfo.spec){
							// 如果存在，修改商品数量
							goods.number += this.goodsInfo.number
							isExist = true;
						}
					})
					// 如果商品不存在
					if(!isExist){
						goodsList.push(this.goodsInfo)
						// 更新本地存储
						this.setGoodsList(goodsList)
					}
					
				}),
				fail:(err) => { // 没有得到数据，那么就存
					let goodsList = []
					goodsList.push(this.goodsInfo)
					// 往本地存储中存储数据
					this.setGoodsList(goodsList)
				}
			})
		},
		setGoodsList(goodsList){
			console.log("11111")
			// 存储到本地存储中
			uni.setStorage({
				key: "goodsList",
				data: goodsList,
				success:function(){
					uni.showToast({
						icon:"success",
						title:"添加购物车成功"
					})
				}
			})
		},
		// 立即购买
		handleBuy(){
			let tempList = []
			tempList.push(this.goodsInfo)
			uni.setStorage({
				key: "confirmList",
				data: tempList,
				success: () => {
					uni.navigateTo({
						url: "../order/confirm"
					})
				}
			})
		}
	}
}
